<template>
   <div class="dealpswd">
        <div class='mine_top'>
            <div class='return' @click='retreat()'></div>
            <span>交易密码</span>
        </div>
        <div class='pswd_cen'>
            <van-cell-group>
                <van-field 
                label="登录密码"
                v-model="password"
                type="password"
                placeholder="请输入您的登录密码" />
            </van-cell-group>
            <van-cell-group>
                <van-field 
                label="交易密码"
                type="password"
                v-model="dealpassword"
                placeholder="请设置您的交易密码" />
            </van-cell-group>
            <van-cell-group>
                <van-field 
                label="确认密码"
                type="password"
                v-model="dealpasswd"
                placeholder="请确认您的交易密码" />
            </van-cell-group>
        </div>
        <van-button size="large" @click='submit()' class='set_but' >确认修改</van-button>
   </div>
</template>

<script>

import axios from 'axios'

export default {
   name: 'dealpswd',
   data() {
       return {
           password:'',
           dealpassword:'',
           dealpasswd:''
       }
   },
   methods:{
        retreat(){
            this.$router.go(-1);
        },
        submit(){
            if( this.password === '' ){
                this.$dialog.alert({
                    message: '请输入您的登录密码'
                })
            }else if( this.dealpassword === '' ){
                this.$dialog.alert({
                    message: '请设置您的交易密码'
                })
            }else if( this.dealpasswd === '' || this.dealpasswd !== this.dealpassword ){
                this.$dialog.alert({
                    message: '请确认您的交易密码'
                })
            }else{
                let url = window.globalData.config.domain + '/userLogin/setNewPayPasswd';
                axios.post(url,{
                    cmd:'102',
                    body:{
                        accessToken:localStorage.token,
                        did:'',
                        passwd:this.password,
                        paypasswd:this.dealpassword,
                        paypasswd_again:this.dealpasswd
                    }
                }).then( (res) =>{
                    // console.log(res);
                    let err = res.data.errmsg;
                    // console.log(err);
                    if( err === 'passwd is wrong' ){
                        this.$dialog.alert({
                            message: '您输入的登录密码有误'
                        })
                    }else if( res.data.body !== '' ){
                        this.$router.go(-1);
                    }
                })
            }
        }
   },
  components: {}
}
</script>

