    width: 100%;
    height: calc(100vh);
    .mine_top{
        width: 100%;
        height: 64px;
        background: white;
        text-align: center;
        line-height: 64px;
        color: black;
        position: fixed;
        z-index: 10;
        border-bottom: 1px solid #ddd;
        .return{
            position: absolute;
            left: 8px;
            width: 32px;
            height: 64px;
            background: url('../../../static/images/icon/arrows3.png') no-repeat center;
            background-size: 24px;
        }
    }
    .pswd_cen{
        width: 100%;
        height: 100%;
        background: #edeff5;
        padding-top: 64px;
        padding-bottom: 64px;
    }
    .set_but{
        position: fixed;
        height: 64px;
        bottom: 0;
